@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400;500;600;700&display=swap");
* {
  font-family: "Fira Sans", sans-serif;
}

button:active {
  transform: scale(1.1);
}

button:hover {
  background: #fe7273;
}

Link:visited {
  text-decoration: none;
  color: #000000;
}

a:hover {
  cursor: pointer;
}

.list {
  display: flex;
  flex-wrap: wrap;
}

.list__title {
  padding-left: 20px;
  padding-top: 20px;
  border-top: 1px solid #e6e6e6;
  width: 137%;
}

